import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ViewController } from 'ionic-angular';
import { Events } from 'ionic-angular';


import { RestProvider } from '../../providers/rest/rest';
import { LoadingController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { LoadingController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { ModalController } from 'ionic-angular';

/**
 * Generated class for the ModalServicesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-modal-services',
  templateUrl: 'modal-services.html',
})
export class ModalServicesPage {

dataUser:any={};
listaServicios:any=[];

  constructor(public viewCtrl : ViewController,public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public storage: Storage,public restProvider: RestProvider,public events: Events,
  public alertCtrl: AlertController, public modalCtrl: ModalController) {
  }

  ionViewDidLoad() {

     this.storage.get('usr_tok_byae').then((result) => {
    
    if(result){
      this.dataUser = result;
      console.log(this.dataUser);


          console.log('ionViewDidLoad ModalServicesPage');
       let loading = this.loadingCtrl.create({content : "Cargando..."});
  loading.present();
var dare = {idEmpleado:this.dataUser.idEmpleado};
console.log(dare);
            this.restProvider.getEmpleadoServicios(dare)
    .then(data => {
    console.log(data);

    this.listaServicios = Object.values(data);
        loading.dismiss();
    },(err) => {

  this.presentAlert('Ups!','Ha ocurrido un error inesperado');
  loading.dismiss();
  console.log(err);
  });



 
    } else {
    console.log('error-nologin');
    }
    });




  }


seleccionarServicio(s){
	console.log(s);
	 this.events.publish('addServicio', s);
	 this.viewCtrl.dismiss();
}



    toggleSection(i) {
    this.information[i].open = !this.information[i].open;
  }
 
  toggleItem(i, j) {
    this.information[i].children[j].open = !this.information[i].children[j].open;
  }
 




public closeModal(){
 //	this.events.publish('modalServices');
    this.viewCtrl.dismiss();
}

getHoras(hora){
 
  return Math.floor(hora/60);
}



}



